.App {
  text-align: center;
  display: flex;
  justify-content: center;
  margin: 0px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.calc-button-container {
  width: 625px;
  height: 700px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  padding: 10px;
  background-color: Grey;
}

.calc-button {
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin: 10px;
  border: 5px solid black;
  background-color: #f1f1f1;
  font-size: 75px;
  border-radius: 38px;
  font-weight: bold;
}

.display-container {
  width: 625px;
  padding: 10px;
  background-color: grey;
}

.display {
  font-family: monospace;
  font-size: 60px;
  font-weight: bold;
  display: flex;
  flex-flow: column;
  justify-content: right;
  align-items: center;
  background-color: #f2f2f2;
  border-radius: 30px;
  margin: 10px;
}

.display-line {
  display: flex;
  width: 95%;
  justify-content: right;
}
